<template>
  <div class="home">
    <div>
      <!--  -->
      <div class="nav">
        <p class="welcome"></p>
        <div class="navigat">
          <ul>
            <li><router-link to="/personal"></router-link></li> 
            <li><router-link to="/shopcar"></router-link></li>    
            <li ><router-link :to="token?'/kefu': '/login'">{{token}}</router-link></li>   
            <li><div @click="logout"></div></li> 
          </ul>
        </div>
      </div>
      <!--  -->
      <div class="oldtitle">
        <div class="h1">
          <h1></h1>
        </div>
        <div class="navtitle">
          <ul>
          <router-link to="/home"></router-link>
            <router-link to="/sell"></router-link>
            <router-link to="/orderList"></router-link>
            <router-link to="/fabued"></router-link>
          </ul>
        </div>
      </div>
      <!--  -->
      <div class="shopmain">
        <div class="mainone">
          <div class="maintwo">
            <div class="myShopCar">
              <h1></h1>
            </div>
            <div class="myShops">
              <el-table :data="shopCarList" stripe style="width: 100%" @selection-change="handleSelectionChange">
                <el-table-column type="selection" width="55" align="center"></el-table-column>
                <el-table-column type="index" label="" width="55" align="center"></el-table-column>
                <el-table-column prop="bookName" label="" width="130" align="center"></el-table-column>
                <el-table-column prop="bookSrc" label="" width="100" align="center">
                  <template scope="scope">
                      <img  :src="'http://49.234.70.67:8081/'+scope.row.bookSrc" style="width: 70px; height: 80px" />
                  </template>
                </el-table-column>
                <el-table-column prop="sellerName" label="" width="120" align="center"></el-table-column>
                <el-table-column prop="sellerStudentCode" label="" width="120" align="center"></el-table-column>
                <el-table-column prop="price" label="/()" width="100" align="center">
                     <template slot-scope="scope">
                          {{scope.row.price.toFixed(2)}}
                      </template>
                </el-table-column>
                <el-table-column prop="count" label="" width="140" align="center">
                      <template slot-scope="scope">
                          <el-input-number @change="handleChange(scope.$index)"  v-model="scope.row.count" :value="null" :min="1" :max="1000" class="edit-input" size="mini" ></el-input-number>
                      </template>
                </el-table-column>
                <el-table-column  label="/()" prop="sTotal" width="100" align="center">
                      <template slot-scope="scope">
                          {{scope.row.count===1?scope.row.price.toFixed(2):scope.row.sTotal.toFixed(2)}}
                      </template>
                </el-table-column>
                <el-table-column label="" width="100" align="center">
                  <template slot slot-scope="scope">
                      <el-button
                        type="danger"
                        icon="el-icon-delete"
                        size="mini"
                        @click="removeParams(scope.row.bookId)"
                      ></el-button>
              </template>
                </el-table-column>
              </el-table>
                <div class="pagination">
                  <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="queryInfo.page"
                    :page-sizes="[3,5,10,15]"
                    :page-size="queryInfo.size"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
                  </el-pagination>
                </div>
            </div>
            <div class="qujiesuan">
                  <div class="populate">
                    <div class="qusuan" @click="deleteAll"></div>
                  </div>
                  <div class="populate">
                    <span><b>{{multipleSelection.length}}</b></span>
                    <span>: <b>{{0||allPrice.toFixed(2)}}</b></span>
                    <div class="qusuan" @click="next">></div>
                  </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- <footer-box></footer-box> -->
  </div>
</template>
<script>
import footer from '../components/footer.vue'
export default {
  data() {
    return {
      shopCarList:[],// 
      multipleSelection: [], //
      // count:1,
      token:'',
      total: 0,
      queryInfo: {
        page: 1, //
        size:5  //
      },
      // money:undefined,
      bookIds:[],//    
      number:[]  //bookId
    };
  },
  components:{
        "footer-box":footer
    },
  created(){
    this.getAllmyBought()
  },
  methods: {
     // 
    async deleteAll(){
      const confirm = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => err);
      if (confirm !== "confirm") {
        return this.$message.info("");
      }
      this.$http({
            url: "http://49.234.70.67:8081/u/delete/buyCar/",
            method: "post",
            crossdomain: true,
            headers: {
                'Content-Type': 'application/json',
                'token':window.sessionStorage.getItem('token')
            },
            data:this.bookIds
            }).then(res => {
              console.log(res)
              if (res.data.status ===200) {
                this.$message.success('');
                this.getAllmyBought()
              }
              if (res.data.status === -1) {
                  this.$message.error(res.data.message);
                  this.$router.push('/login')
              }
            }).catch(res=>{
              console.log('error')
            })
    },
     // 
      handleSelectionChange(val) {
          this.multipleSelection = val; //
          // console.log(this.multipleSelection)
          for(let i = 0;i<this.multipleSelection.length;i++){
            if(this.multipleSelection[i].count===undefined){
              this.$message.info('')
            }
            if(this.bookIds.indexOf(this.multipleSelection[i].bookId)==-1){
                this.bookIds.push(this.multipleSelection[i].bookId)
                this.number.push(this.multipleSelection[i].count)
            }
          }
      },
      //
			handleChange: function(index) {
          this.shopCarList[index].sTotal = (this.shopCarList[index].count * this.shopCarList[index].price);
				// console.log(this.shopCarList[index])
				//product_Update_info
				// this.product_Update_info.push(this.shopCarList[index])
			},
    // 
    getAllmyBought(){
          this.$http({
            url: "http://49.234.70.67:8081/u/search/buyCar",
            method: "post",
            crossdomain: true,
            headers: {
                'Content-Type': 'application/json',
                'token':window.sessionStorage.getItem('token')
            },
            data:this.queryInfo
            }).then(res => {
              console.log(res)
              //  
              // el-input-number 
              res.data.data[0].forEach(item => {
                item.count = 1
              })
              this.shopCarList = res.data.data[0]
              // console.log(this.shopCarList)
              this.total = res.data.data[1].total
              if(!res.config.headers.token){
                this.token = '/'
              } 
              if (res.data.status === -1) {
                  this.$message.error(res.data.message);
                  this.$router.push('/login')
              }
            }).catch(res=>{
              console.log('')
            })
    },
    // 
    async removeParams(bookId){
      const confirm = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => err);
      if (confirm !== "confirm") {
        return this.$message.info("");
      }
      this.$http({
            url: "http://49.234.70.67:8081/u/delete/buyCar/",
            method: "post",
            crossdomain: true,
            headers: {
                'Content-Type': 'application/json',
                'token':window.sessionStorage.getItem('token')
            },
            data:bookId.toString().split(" ")
            }).then(res => {
              console.log(res)
              this.getAllmyBought()
              if (res.data.status === -1) {
                  this.$message.error(res.data.message);
                  this.$router.push('/login')
              }
            }).catch(res=>{
              console.log('error')
            })
    },
    next(){
      for(let i = 0;i<this.multipleSelection.length;i++){
          if(this.multipleSelection[i].count===undefined){
            return this.$message.info('')
          }
      }
      if(this.multipleSelection.length===0){
        return this.$message.error('')
      }else{
        this.$router.push({path: '/order',
        query:{
            bookIds:this.bookIds,
            number:this.number,
            // allPrice:allPrice
      }})
      }
    },
    // 
    logout(){
        this.$http({
            url: "http://49.234.70.67:8081/u/logout",
            method: "get",
            crossdomain: true,
            headers: {
                'token':window.sessionStorage.getItem('token')
            }
            }).then(res => {
              console.log(res)
              window.sessionStorage.clear();
              this.$router.push("/login");
              if (res.data.status === -1) {
                  this.$message.error(res.data.message);
                  this.$router.push('/login')
              }
            }).catch(res=>{
              console.log('')
            })
    },
    // 
    handleSizeChange(newsize) {
        this.queryInfo.size = newsize;
        this.getAllmyBought();
    },
    handleCurrentChange(newnum) {
        this.queryInfo.page = newnum;
        this.getAllmyBought();
    },
  },
  computed:{
    //
			allPrice(){
        // let Sum = 0;
        // console.log(this.shopCarList)
        // sTotal  undefined
        var price_total = 0;
        for (var i = 0; i < this.multipleSelection.length; i++) {
          price_total +=
           this.multipleSelection[i].price * this.multipleSelection[i].count;
          }
      return price_total;
				// 3  , 
        // return Sum.toString().replace(/\B(?=(\d{3})+$)/g, ',');
        // return Sum
			}
  },
};
</script>
<style  scoped>
.oldtitle {
  width: 100%;
  height: 80px;
  background-color: #fff;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.oldtitle .h1 {
  color: rgb(1, 200, 181);
}
.navtitle ul a {
  text-decoration: none;
  color: rgb(100, 100, 100);
  font-size: 18px;
  font-weight: bold;
  margin-right: 70px;
}
.navtitle ul a:hover {
  color: rgb(1, 200, 181);
}
/*  */
.shopmain .mainone {
  background-color: rgb(245, 245, 245);
  width: 100%;
  /* border:1px red solid; */
  /* height: 500px; */
  padding: 10px;
}
.shopmain .maintwo {
  margin: 0 auto;
  width: 1000px;
  height: 40px;
  background-color: #fff;
  padding: 15px;
}
.myShopCar h1 {
  color: rgb(119, 119, 119);
}
.maintwo .myShops {
  width: 1035px;
  margin: 20px auto;
  margin-left: -17px;
  background-color: #fff;
}
.qujiesuan{
  display: flex;
  width:inherit;
  border:1px #fff solid;
  height: 17px;
  justify-content:space-between;
  align-items: center;
  margin-top:-21px;
  box-sizing: content-box;
  padding:18px;
  margin-left: -18px;
  color:rgb(144,147,153)
}
.qujiesuan b{
  color:red
}
.populate{
  width:300px;
  display: flex;
  align-items: center;
  justify-content:space-between;
}
.populate .qusuan{
  background-color: rgb(1, 200, 181);
  height: 25px;
  color:#fff;
  padding:5px;
  border-radius: 3px;
  font-weight: bold;
}
.pagination .el-pagination{
  margin:20px;
  margin-bottom:20px;
}
.el-input-number{
  width:90px
}
</style>