<template>
  <div class="home">
    <div>
      <!--  -->
      <div class="nav">
        <p class="welcome"></p>
        <div class="navigat">
          <ul>
            <li><router-link to="/personal"></router-link></li> 
            <li><router-link to="/shopcar"></router-link></li>    
            <li ><router-link :to="token?'/kefu': '/login'">{{token}}</router-link></li>   
            <li><div @click="logout"></div></li> 
          </ul>
        </div>
      </div>
      <!--  -->
      <div class="oldtitle">
        <div class="h1">
          <h1></h1>
        </div>
        <div class="navtitle">
          <ul>
            <router-link to="/home"></router-link>
            <router-link to="/sell"></router-link>
            <router-link to="/orderList"></router-link>
            <router-link to="/fabued"></router-link>
          </ul>
        </div>
      </div>
      <!--  -->
      <div class="shopmain">
        <div class="mainone">
          <div class="maintwo">
            <div class="myShopCar">
              <h1></h1>
            </div>
            <div class="myShops">
              <el-table :data="selltableList" stripe style="width: 100%" class="tableBox" >
                <el-table-column type="index" label="" width="55" align="center"></el-table-column>
                <el-table-column prop="bookName" label="" width="120" align="center"></el-table-column>
                <el-table-column  label="" width="150" align="center">
                      <template slot-scope="scope">
                        <el-image
                        style="width: 100px; height: 100px"
                        :src="'http://49.234.70.67:8081/'+scope.row.bookSrc"></el-image>
                      </template>
                </el-table-column>
                <el-table-column prop="bookIntroduction" label="" width="130" align="center"></el-table-column>
                <el-table-column prop="bookStyle" label="" width="120" align="center">
                    <template slot-scope="scope">{{scope.row.bookStyle.join('')}}</template>
                </el-table-column>
                <el-table-column prop="bookDate" label="" width="120" align="center">
                  <template slot-scope="scope">{{scope.row.bookDate|dataFormat}}</template>
                </el-table-column>
                <el-table-column prop="price" label="" width="100" align="center">
                  <template slot-scope="scope">
                          {{scope.row.price.toFixed(2)}}
                  </template>
                </el-table-column>
                <el-table-column prop="stock" label="" width="100" align="center">
                  <template slot-scope="scope">{{scope.row.stock}}</template>
                </el-table-column>
                <el-table-column label="" width="140px" align="center">
                <template slot-scope="scope">
                  <el-button type="primary" icon="el-icon-edit" size="mini"
                  @click="modefyBooks(scope.row.bookId)">
                    </el-button>
                </template>
              </el-table-column>
              </el-table>
              <!--  -->
              <el-dialog title="" :visible.sync="dialogFormVisible">
                <el-form :model="{modefyList}">
                  <el-form-item label="" >
                    <el-input v-model="modefyList.bookName" autocomplete="off"></el-input>
                  </el-form-item>
                  <el-form-item label="" >
                    <textarea class="textarea"  v-model="modefyList.bookIntroduction"  name id cols="51" rows="10" placeholder="(,200)"></textarea>
                  </el-form-item>
                  <el-form-item label="" >
                      <el-input-number v-model="modefyList.price" :precision="2" :step="0.1" :max="100000"></el-input-number>
                  </el-form-item>
                  <el-form-item label="" >
                    <el-select v-model="modefyList.bookStyle" multiple placeholder="">
                      <el-option
                        v-for="item in options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                      </el-option>
                  </el-select>
                  </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                  <el-button @click="dialogFormVisible = false"> </el-button>
                  <el-button type="primary" @click="editBooks(modefyList.bookId)"> </el-button>
                </div>
              </el-dialog>
              <!--  -->
              <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="queryInfo.page"
                    :page-sizes="[3,5,10,15]"
                    :page-size="queryInfo.size"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
                  </el-pagination>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import footer from '../components/footer.vue'
export default {
    data() {
        return {
            token:'',
            total: 30,
            AllMyFabuds:{},
            selltableList:[],
            queryInfo: {
                page: 1, //
                size:5  //
            },
            srcList:'',
            dialogFormVisible:false,
            modefyList:[], //
            // elTableH:null,  //elTableHnull   
            options: [{
              value: '',
              label: ''
            }, {
              value: '',
              label: ''
            }, {
              value: '',
              label: ''
            }, {
              value: '',
              label: ''
            }, {
              value: '',
              label: ''
            }, {
              value: '',
              label: ''
            }, {
              value: '',
              label: ''
            }, {
              value: '',
              label: ''
            }, {
              value: '',
              label: ''
            }, {
              value: '',
              label: ''
            }],
        };
    },
    components:{
        "footer-box":footer
    },
    mounted(){
        // 
        this.getAllMyFabud()
        // this.getTbHeight()
    },
    // beforeMount(){
    //     window.addEventListener('resize',this.getTbHeight)
    // },
    methods: {
        // table
        // getTbHeight(){
        //     this.elTableH = window.innerHeight-100
        // },
        // 
        getAllMyFabud(){
            this.$http({
            url:'http://49.234.70.67:8081/u/search/book/own',
            method: "post",
            crossdomain: true,
            data:{
                page:this.queryInfo.page,
                size:this.queryInfo.size
            },
            headers: {
                'Content-Type': 'application/json',
                'token':window.sessionStorage.getItem('token')
            },
            }).then(res => {
                // console.log(params)
                console.log(res)
                if(!res.config.headers.token){
                  this.token = '/'
                } 
                this.selltableList = res.data.data[0]
                this.srcList = this.selltableList[0].bookSrc
                // console.log('selltableList',this.selltableList)
                if (res.data.status === -1) {
                    this.$message.error(res.data.message);
                    this.$router.push('/login')
                }
            }).catch(res=>{
                console.log('')
            })
        },
        // 
        modefyBooks(bookId){
            this.dialogFormVisible = true
              this.$http({
              url: `http://49.234.70.67:8081/u/search/book/${bookId}`,
              method: "get",
              crossdomain: true,
              headers: {
                  'Content-Type': 'application/json',
                  'token':window.sessionStorage.getItem('token')
              },
              }).then(res => {
                console.log(res)
                if (res.data.status === 200) {
                    this.modefyList = res.data.data[0]
                }
                if (res.data.status === -1) {
                    this.$message.error(res.data.message);
                    this.$router.push('/login')
                }
              }).catch(res=>{
                console.log('error')
              })
        },
        //   
        editBooks(bookId){
        this.dialogFormVisible = false
        this.$http({
              url: 'http://49.234.70.67:8081/u/update/book',
              method: "post",
              crossdomain: true,
              headers: {
                  'Content-Type': 'application/json',
                  'token':window.sessionStorage.getItem('token')
              },
              data:{
                bookId:bookId,
                bookName:this.modefyList.bookName,
                bookIntroduction:this.modefyList.bookIntroduction,
                price:this.modefyList.price,
                bookStyle:this.modefyList.bookStyle
              }
              }).then(res => {
                console.log(res)
                // if (res.data.status === 200) {
                  // 
                    this.getAllMyFabud()
                    this.$message.success('');
                // }
                if (res.data.status === -1) {
                    this.$message.error(res.data.message);
                    this.$router.push('/login')
                }
              }).catch(res=>{
                console.log('error')
              })
        },
       // 
        handleSizeChange(newsize) {
          this.queryInfo.size = newsize;
          this.getAllMyFabud();
        },
        handleCurrentChange(newnum) {
          this.queryInfo.page = newnum;
          this.getAllMyFabud();
        },
        // 
        logout(){
         this.$http({
            url: "http://49.234.70.67:8081/u/logout",
            method: "get",
            crossdomain: true,
            headers: {
                'token':window.sessionStorage.getItem('token')
            }
            }).then(res => {
              console.log(res)
              window.sessionStorage.clear();
              this.$router.push("/login");
              if (res.data.status === -1) {
                  this.$message.error(res.data.message);
                  this.$router.push('/login')
              }
            }).catch(res=>{
              console.log('')
            })
         },
    }
};
</script>
<style  scoped>
.oldtitle {
  width: 100%;
  height: 80px;
  background-color: #fff;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.oldtitle .h1 {
  color: rgb(1, 200, 181);
}
.navtitle ul a {
  text-decoration: none;
  color: rgb(100, 100, 100);
  font-size: 18px;
  font-weight: bold;
  margin-right: 70px;
}
.navtitle ul a:hover {
  color: rgb(1, 200, 181);
}
/*  */
.shopmain .mainone {
  background-color: rgb(245, 245, 245);
  width: 100%;
  /* border:1px red solid; */
  /* height: 500px; */
  padding: 10px;
}
.shopmain .maintwo {
  margin: 0 auto;
  width: 1000px;
  height: 40px;
  background-color: #fff;
  padding: 15px;
}
.myShopCar h1 {
  color: rgb(119, 119, 119);
}
.maintwo .myShops {
  width: 1035px;
  margin: 20px auto;
  margin-left: -17px;
  background-color: #fff;
  padding:20px;
}
.tableBox th{
  padding:0 !important;
  max-height:200px; 
}
.tableBox td{
  padding:0 !important;
  max-height: 200px; 
}
.el-pagination{
  margin:10px;
}
